import React, { useState } from 'react';
import { Form, Input, Button, Typography, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import './Login.scss';

const { Title, Text } = Typography;

const Login = () => {
  const [loading, setLoading] = useState(false);

  const onFinish = (values) => {
    setLoading(true);
    console.log('Received values of form: ', values);
    // Simulate API call
    setTimeout(() => {
      setLoading(false);
      message.success('Login successful!');
    }, 1500);
  };

  return (
    <div className="login-page">
      <div className="login-card">
        <div style={{ textAlign: 'center' }}>
          <Title level={2} className="title">CCTV CONTROL</Title>
          <Text className="subtitle">Secure System Access</Text>
        </div>
        
        <Form
          name="login_form"
          initialValues={{ remember: true }}
          onFinish={onFinish}
          layout="vertical"
          size="large"
          style={{ marginTop: '30px' }}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: 'Please input your access ID!' }]}
          >
            <Input 
              prefix={<UserOutlined style={{ color: 'rgba(255,255,255,0.5)' }} />} 
              placeholder="Access ID" 
            />
          </Form.Item>

          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <Input.Password 
              prefix={<LockOutlined style={{ color: 'rgba(255,255,255,0.5)' }} />} 
              placeholder="Password" 
            />
          </Form.Item>

          <Form.Item>
            <Button 
              type="primary" 
              htmlType="submit" 
              className="login-button"
              loading={loading}
            >
              Log In
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default Login;
